import React, { useState, useEffect } from 'react';

// seo purposes
import { Helmet } from 'react-helmet';

// 
import { TextField, Button, Container, Stack, styled, Typography, Slider, Box } from '@mui/material';

// animations
import { neonSxProps } from '../animations/neon';

// theme wrapper
import ThemeWrapper from '../theme/Wrapper';
import Header from '../components/Header';
import BinauralBeatsGenerator from './BeatsGenerator';

const RootContainer = styled(Container)({
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '16px',
    backgroundColor: 'background.paper',
    borderRadius: '2em',
    boxShadow: '0 0 9px 2px rgba(155,155,155,0.25)',
    marginTop: '5em',
    overflow: 'scroll',
    height: 'auto',
    minHeight: '400px',
    width: '95%',
    margin: 'auto',
});

const InputContainer = styled(Box)(({ theme }) => ({
    width: '100%',
    marginBottom: theme.spacing(2),
}));

const BinauralBeats: React.FC = () => {
    const [baseValue, setBaseValue] = useState<number>(200);
    const [frequencyValue, setFrequencyValue] = useState<number>(5);
    const [activeStep, setActiveStep] = useState<number>(1);
    const [isPlaying, setIsPlaying] = useState<boolean>(false);


    const audioContext = new (window.AudioContext)();
    let oscillator: OscillatorNode | null = null;

    const handleNextStep = (): void => {
        setActiveStep((prevStep) => prevStep + 1);
    };

    const handlePrevStep = (): void => {
        setActiveStep((prevStep) => prevStep - 1);
    };

    const incrementBaseValue = (): void => {
        if (isPlaying) {
            stopAudio();
        }
        setBaseValue((prevValue) => prevValue + activeStep);
    };

    const decrementBaseValue = (): void => {
        if (isPlaying) {
            stopAudio();
        }
        setBaseValue((prevValue) => prevValue - activeStep);
    };

    useEffect(() => {
        if (isPlaying) {
            // oscillator = audioContext.createOscillator();
            // const merger = audioContext.createChannelMerger(2);

            // oscillator.type = 'sine';
            // oscillator.frequency.value = baseValue + frequencyValue;

            // oscillator.connect(merger, 0, 0);
            // oscillator.connect(merger, 0, 1);
            // merger.connect(audioContext.destination);

            // oscillator.start();

            // make using FM
            // Create the left oscillator
            const oscillatorLeft = audioContext.createOscillator();
            oscillatorLeft.frequency.value = baseValue - frequencyValue;
            oscillatorLeft.start();

            // Create the right oscillator
            const oscillatorRight = audioContext.createOscillator();
            oscillatorRight.frequency.value = baseValue;
            oscillatorRight.start();

            // Create the modulator oscillator
            const modulator = audioContext.createOscillator();
            modulator.frequency.value = frequencyValue;
            modulator.start();

            // Connect the modulator to the frequency of the right oscillator
            oscillatorRight.frequency.setValueAtTime(baseValue, audioContext.currentTime);
            modulator.connect(oscillatorRight.frequency);

            // Connect the oscillators to the destination (output)
            oscillatorLeft.connect(audioContext.destination);
            oscillatorRight.connect(audioContext.destination);
        }

        return () => {
            stopOscillator();
        };
    }, [isPlaying, baseValue, frequencyValue]);

    const startAudio = (): void => {
        setIsPlaying(true);
    };

    const stopAudio = (): void => {
        setIsPlaying(false);
    };

    const stopOscillator = (): void => {
        if (oscillator) {
            oscillator.stop();
            oscillator = null;
        }
    };

    const handleFrequencyValueChange = (event: Event, newValue: number | number[]): void => {
        setFrequencyValue(newValue as number);
    };

    const formatFrequencyValueLabel = (value: number): string => {
        if (value >= 1 && value <= 4) {
            return `Delta: ${frequencyValue}`;
        }
        if (value >= 4 && value <= 8) {
            return `Theta: ${frequencyValue}`;
        }
        if (value >= 8 && value <= 13) {
            return `Alpha: ${frequencyValue}`;
        }
        if (value >= 14 && value <= 30) {
            return `Beta: ${frequencyValue}`;
        }
        return `Custom: ${frequencyValue}`;
    };


    return (
        <ThemeWrapper >
            <Container
                sx={{
                    display: 'grid',
                    placeItems: 'center',
                    height: '100vh',
                    backgroundColor: 'background.default',
                    width: '100vw',
                }}
            >

                <Header title='Binaural Beats Generator' />

                <Container
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        padding: '16px',
                        backgroundColor: 'background.paper',
                        borderRadius: '2em',
                        width: '95vw',
                    }}
                >
                    <Typography variant='h5' my={3} >
                        Controls
                    </Typography>

                    <BinauralBeatsGenerator />

                </Container>

            </Container>
        </ThemeWrapper>
    );
};

export default BinauralBeats;
