import React, { Component, ChangeEvent } from 'react';
import { Slider, Button, Typography, Box, Stack, IconButton } from '@mui/material';

import SliderComp from '../components/Slider';
import { PlayArrow, StopCircle } from '@mui/icons-material';

interface State {
    frequencyA: number;
    frequencyB: number;
    isPlaying: boolean;
    presets: { frequencyA: number; frequencyB: number; description: string }[];
    audioContext: AudioContext | null;
    oscillatorA: OscillatorNode | null;
    oscillatorB: OscillatorNode | null;
    gainNodeA: GainNode | null;
    gainNodeB: GainNode | null;
}


class BinauralBeatsGenerator extends Component<{}, State> {
    constructor(props: {}) {
        super(props);
        this.state = {
            frequencyA: 200, // Default frequency for left ear
            frequencyB: 210, // Default frequency for right ear
            isPlaying: false, // Controls the beats generation
            presets: [
                { frequencyA: 8, frequencyB: 12, description: 'Alpha Waves - Relaxation' },
                { frequencyA: 13, frequencyB: 30, description: 'Beta Waves - Focus and Concentration' },
                { frequencyA: 4, frequencyB: 8, description: 'Theta Waves - Deep Meditation' },
                { frequencyA: 1, frequencyB: 4, description: 'Delta Waves - Sleep and Deep Relaxation' },
                { frequencyA: 40, frequencyB: 100, description: 'Gamma Waves - Cognitive Enhancement' },
            ],
            audioContext: null,
            oscillatorA: null,
            oscillatorB: null,
            gainNodeA: null,
            gainNodeB: null
        };
    };

    componentDidMount() {
        // Effect to handle beats generation when frequencies change or isPlaying state changes
        this.generateBinauralBeats();
    };

    componentDidUpdate(prevProps: {}, prevState: State) {
        // Effect to handle beats generation when frequencies change or isPlaying state changes
        if (
            prevState.frequencyA !== this.state.frequencyA ||
            prevState.frequencyB !== this.state.frequencyB ||
            prevState.isPlaying !== this.state.isPlaying
        ) {
            this.generateBinauralBeats();
        }
    };

    componentWillUnmount() {
        // Clean up the effect when component unmounts or frequencies change
        this.stopBinauralBeats();
    };

    generateBinauralBeats() {
        if (this.state.isPlaying) {
            const { frequencyA, frequencyB } = this.state;

            // Generate the binaural beats using the selected frequencies
            const audioContext = new (window.AudioContext)();
            const oscillatorA = audioContext.createOscillator();
            const oscillatorB = audioContext.createOscillator();
            const gainNodeA = audioContext.createGain();
            const gainNodeB = audioContext.createGain();

            oscillatorA.frequency.value = frequencyA;
            oscillatorB.frequency.value = frequencyB;

            oscillatorA.type = 'sine';
            oscillatorB.type = 'sine';

            oscillatorA.connect(gainNodeA);
            oscillatorB.connect(gainNodeB);

            gainNodeA.connect(audioContext.destination);
            gainNodeB.connect(audioContext.destination);

            oscillatorA.start();
            oscillatorB.start();

            gainNodeA.gain.value = 0.5;
            gainNodeB.gain.value = 0.5;

            this.setState({ audioContext, oscillatorA, oscillatorB, gainNodeA, gainNodeB });
        } else {
            // Stop the binaural beats
            const { audioContext, oscillatorA, oscillatorB, gainNodeA, gainNodeB } = this.state;

            oscillatorA?.stop();
            oscillatorB?.stop();

            gainNodeA?.disconnect();
            gainNodeB?.disconnect();

            audioContext?.close();

            this.setState({
                audioContext: null,
                oscillatorA: null,
                oscillatorB: null,
                gainNodeA: null,
                gainNodeB: null,
            });
        }
    };

    stopBinauralBeats() {
        const { oscillatorA, oscillatorB, gainNodeA, gainNodeB, audioContext } = this.state;

        if (oscillatorA && oscillatorB && gainNodeA && gainNodeB && audioContext) {
            oscillatorA.stop();
            oscillatorB.stop();

            gainNodeA.disconnect();
            gainNodeB.disconnect();

            audioContext.close();
        }
    };

    handleFrequencyAChange = (event: any, newValue: number | number[]) => {
        this.setState({ frequencyA: newValue as number });
    };

    handleFrequencyBChange = (event: any, newValue: number | number[]) => {
        this.setState({ frequencyB: newValue as number });
    };

    handleTogglePlay = () => {
        this.setState((prevState) => ({ isPlaying: !prevState.isPlaying }));
    };

    handlePresetSelect = (preset: { frequencyA: number; frequencyB: number; description: string }) => {
        this.stopBinauralBeats();
        this.setState({
            frequencyA: preset.frequencyA,
            frequencyB: preset.frequencyB,
        });
        this.generateBinauralBeats();
    };

    render() {
        return (
            <Box
                sx={{
                    display: 'flex',
                    width: '100%',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    backgroundColor: 'background.paper',
                }}
            >

                <Typography variant="h6" gutterBottom>
                    Frequency A: {this.state.frequencyA} Hz
                </Typography>
                <Slider
                    value={this.state.frequencyA}
                    onChange={this.handleFrequencyAChange}
                    min={100}
                    max={500}
                    step={10}
                    sx={{ width: 300 }}
                />

                <Typography variant="h6" gutterBottom>
                    Frequency B: {this.state.frequencyB} Hz
                </Typography>
                <Slider
                    value={this.state.frequencyB}
                    onChange={this.handleFrequencyBChange}
                    min={100}
                    max={500}
                    step={10}
                    sx={{ width: 300 }}
                />

                <Box sx={{
                    width: '100%',
                    overflowX: 'scroll',
                    scrollSnapType: 'x mandatory',
                    padding: '16px 12px',
                    display: 'flex',
                }}>
                    <Stack direction="row" gap={1} >
                        {
                            this.state.presets.map((preset, index) => (
                                <Button
                                    key={index}
                                    variant="contained"
                                    onClick={() => this.handlePresetSelect(preset)}
                                >
                                    <Typography variant="body2">
                                        {preset.description}
                                    </Typography>
                                </Button>
                            ))
                        }
                    </Stack>
                </Box>

                <IconButton
                    onClick={this.handleTogglePlay}
                    sx={{ marginTop: 20 }}
                >
                    {
                        this.state.isPlaying ?
                            <StopCircle /> :
                            <PlayArrow />
                    }
                    {this.state.isPlaying ? 'Stop' : 'Start'}
                </IconButton>

            </Box>
        );
    }
}

export default BinauralBeatsGenerator;
