interface HeaderProps extends PropsWithChildren {
    title?: string;
}


const Header: React.FC<HeaderProps> = ({
    title = "Beyond Digital",
    children
}) => {
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
    const [scrollY, setScrollY] = useState(0);
    const [open, setOpen] = useState(false);

    const handleScroll = () => {
        setScrollY(window.scrollY);
    };

    const handleToggleOpen = () => {
        setOpen(!open);
    };

    React.useEffect(() => {
        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <AnimatedHeaderContainer
            position="fixed"
            elevation={scrollY > 0 ? 4 : 0}
            sx={{
                backgroundColor: scrollY > 0 ? theme.palette.background.paper : 'transparent',
                color: theme.palette.text.primary,
                boxShadow: (scrollY > 0 || open) ? theme.shadows[7] : 'none',
                borderRadius: open ? "0 0 1em 1em" : "0",
                transition: "border-radius 1000ms ease",
                overflow: "hidden"
            }}
        >
            <Toolbar sx={{ justifyContent: 'space-between' }}>
                <AnimatedTypography variant="h6" color="inherit">
                    {title}
                </AnimatedTypography>

                <Stack direction="row" gap={1} >
                    <ThemeSwitch />

                    {isMobile && (
                        <BurgerMenu open={open} onClick={handleToggleOpen} >
                            <span></span>
                            <span></span>
                            <span></span>
                        </BurgerMenu>
                    )}
                </Stack>
            </Toolbar>

            {isMobile && (
                <Collapse
                    in={open}
                    sx={{ backgroundColor: theme.palette.background.paper }}>
                    <Box sx={{ padding: "2em" }} >
                        <Stack gap={1} mt={2} >
                            {/* {["Home", "About", "Services", "Contact"].map(
                                (text, index) => (
                                    <Button
                                        key={text}
                                        color="inherit"
                                    >
                                        <NavTypography
                                            visible={open}
                                            index={index}
                                            variant="body1"
                                            children={text}
                                        />
                                    </Button>
                                ))
                            } */}
                            {children}
                        </Stack>
                    </Box>
                </Collapse>
            )}

        </AnimatedHeaderContainer>
    );
};

export default Header;
